'use client';

import { createContext, useContext, useEffect, useState } from 'react';
import {
    onAuthStateChanged,
    GoogleAuthProvider,
    signInWithPopup,
    signOut as firebaseSignOut, // Renombrado para evitar conflicto
    signInWithEmailAndPassword,
    createUserWithEmailAndPassword,
    sendPasswordResetEmail,
    updatePassword,
    EmailAuthProvider,
    reauthenticateWithCredential
} from 'firebase/auth';
import { auth } from '@/lib/firebase';
import { useRouter } from 'next/navigation';

const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);

export const AuthProvider = ({ children }) => {
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);
    const router = useRouter();

    useEffect(() => {
        const unsubscribe = onAuthStateChanged(auth, async (user) => {
            setUser(user);
            setLoading(false);
            // Sincronizar la cookie de sesión cada vez que el estado de autenticación cambie
            if (user) {
                const idToken = await user.getIdToken();
                // Llamada a nuestra API para crear la cookie
                await fetch('/api/auth/session', {
                    method: 'POST',
                    headers: {
                        'Authorization': `Bearer ${idToken}`,
                    },
                });
            } else {
                // Llamada a nuestra API para borrar la cookie
                await fetch('/api/auth/session', {
                    method: 'DELETE',
                });
            }
        });

        return () => unsubscribe();
    }, []);

    const loginWithGoogle = async () => {
        const provider = new GoogleAuthProvider();
        // El manejo del token y la cookie ya se hace en onAuthStateChanged
        return signInWithPopup(auth, provider);
    };

    const loginWithEmail = async (email, password) => {
        // El manejo del token y la cookie ya se hace en onAuthStateChanged
        return signInWithEmailAndPassword(auth, email, password);
    };

    const registerWithEmailAndPassword = async (email, password) => {
        // El manejo del token y la cookie ya se hace en onAuthStateChanged
        return createUserWithEmailAndPassword(auth, email, password);
    };
    
    const signOut = async () => {
        // El manejo para borrar la cookie ya se hace en onAuthStateChanged
        return firebaseSignOut(auth);
    };

    const resetPassword = (email) => {
        return sendPasswordResetEmail(auth, email);
    };

    const reauthenticate = async (currentPassword) => {
        const credential = EmailAuthProvider.credential(user.email, currentPassword);
        return reauthenticateWithCredential(user, credential);
    };

    const changePassword = (newPassword) => {
        return updatePassword(user, newPassword);
    };


    const value = {
        user,
        loading,
        loginWithGoogle,
        loginWithEmail,
        registerWithEmailAndPassword,
        signOut,
        resetPassword,
        reauthenticate,
        changePassword,
        // No se necesita el rol aquí, se obtendrá en el servidor
    };

    return (
        <AuthContext.Provider value={value}>
            {!loading && children}
        </AuthContext.Provider>
    );
};
